@import "https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200;400&display=swap";
body {
  background-color: #f0f8ff;
  justify-content: center;
  font-family: Roboto Mono, monospace;
  display: flex;
}

.content {
  min-width: 40rem;
}

@media (max-width: 400px) {
  .content {
    min-width: 80%;
  }
}

.content__search {
  background-color: #fff;
  display: flex;
}

.content__card {
  max-width: 40rem;
  background-color: #d5dfe8;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
  padding: .5rem;
  display: flex;
}

.content__card-item {
  background-color: #aab5bf;
  border: 5px solid #93a0ad;
  margin: .3rem;
  padding: 1rem 1.2rem;
}

.content__card-item--shimer {
  width: 256px;
  height: 256px;
  background-color: #d5dfe8;
}

.content__button {
  letter-spacing: 5px;
  cursor: pointer;
  color: #fff;
  background-color: #b0c4de;
  border: none;
  margin: .5rem;
  padding: 0 1rem;
}

.content__button:hover {
  color: #f5f5f5;
  background-color: #8a9fde;
}

@media (max-width: 400px) {
  .content__button {
    letter-spacing: 2px;
    padding: 0 .3rem;
  }
}

input {
  border: none;
  flex-grow: 2;
  padding: 1.5rem;
  font-family: inherit;
}

input:focus {
  outline: none;
}

.content__search:focus-within {
  outline: 1px solid #a9a9a9;
}

.header {
  color: #fff;
  background-color: #8a9fde;
